@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Raleway';
}
p{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.App
{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  padding:60px 30px 0px 30px ;
  flex:1.5 1;
  flex:var(--sidebar-flex) 1;
  background: rgb(129, 149, 80);
  background: linear-gradient(180deg, rgba(129, 149, 80,1) 50%, rgba(61, 83, 79,1) 80%);
}
.condiv
{
  padding: 100px ;
  background-color: #AFC2CC;
  flex:6 1;
  flex:var(--condiv-flex) 1;
}
.home
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-image: url(/react-portfolio/static/media/background.d6bd8ff1.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}
.home p {
  font-size: 2em;
}
.contact
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.profilepic
{
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}
.typingeffect
{
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
}

nav ul{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li
{
  margin:40px;
}

a
{
  text-decoration: none;
  color: white;
}

.subtopic
{
  margin:10px 10px 30px 10px !important;
  text-decoration: underline;
}
.social
{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}
.social i
{
  padding-right: 30px;
  font-size: 35px;
  color: white;
}
.social i:hover
{
  color: #ffffff;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}
.tablist
{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
.rc-tabs {
  border: none !important;
}
.rc-tabs-nav-wrap {
  background-color:#AFC2CC;
}
.rc-tabs-tab {
  padding: 5px;
  text-transform: capitalize;
  background:#AFC2CC!important;
}
.rc-tabs-tab .rc-tabs-tab-btn {
  font-weight: normal;
}
.rc-tabs-tab.rc-tabs-tab-active {
  color:rgb(129, 149, 80);
}
.rc-tabs-ink-bar {
  background: rgb(129, 149, 80)!important;
}
.rc-tabs-content-holder {
  padding: 10px;
}
.overlay {
  font-weight: bold ;
  color: white;
  text-align: center;
}
.overlay .overlaytext {
  visibility: hidden;
  padding-top: 60px;
}
.overlay:hover {
  background-color: black;
  opacity: 0.5;
}
.overlay:hover .overlaytext {
  visibility: visible;
}
@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  .condiv
  {
    padding: 50px ;
  }
  nav ul
  {
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }
  .social i
  {
    padding-right: 0px;
  }
}
